body {
  font-family: Arial, sans-serif;
  background-color: #f8f5ec;
  margin: 0;
  padding: 0;
}

.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.upload-container {
  margin-bottom: 20px;
}

.upload-button {
  background-color: #4f5b66;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.upload-button:hover {
  background-color: #3a4651;
}

.image-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  max-width: 1200px;
  gap: 20px;
  margin-bottom: 20px;
}

.original-container,
.processed-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 45%;
  max-width: 500px;
  gap: 10px;
}

.placeholder,
.processed-placeholder {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 300px;
  background-color: #3a4651;
  color: #b0bec5;
  font-size: 1.5em;
  border-radius: 10px;
  overflow: hidden;
}

.uploaded-image {
  max-width: 100%;
  max-height: 100%;
  border-radius: 10px;
  object-fit: cover;
}

h3 {
  font-size: 1.5em;
  color: #4f5b66;
  margin: 0;
  text-align: center;
}

.controls-container {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
}

.control-button {
  background-color: #4f5b66;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.control-button:hover {
  background-color: #3a4651;
}

.results-container {
  width: 100%;
  max-width: 1200px;
  padding: 20px;
  background-color: #b0bec5;
  color: #3a4651;
  border-radius: 10px;
  text-align: left;
  font-size: 1.2em;
  overflow-x: auto;
}

/* Heatmap Section */
.heatmap-container {
  width: 100%;
  max-width: 1200px;
  text-align: center;
  margin: 20px 0;
}

.heatmap {
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
}

.heatmap-image {
  max-width: 100%;
  height: auto;
  border-radius: 10px;
}

/* Heatmap Legend */
.legend-container {
  margin: 10px 0;
  text-align: center;
}

.legend-gradient {
  width: 80%;
  height: 20px;
  margin: 0 auto;
  background: linear-gradient(to right, blue, green, yellow, red);
  border: 1px solid #ccc;
  position: relative;
}

.legend-gradient span {
  position: absolute;
  top: 25px;
  font-size: 12px;
  color: #555;
}

.legend-gradient span:nth-child(1) {
  left: 0;
}

.legend-gradient span:nth-child(2) {
  left: 50%;
  transform: translateX(-50%);
}

.legend-gradient span:nth-child(3) {
  right: 0;
}

/* Progress Bar */
.progress-container {
  width: 100%;
  max-width: 600px;
  margin: 20px 0;
}

.progress-bar {
  width: 100%;
  height: 10px;
  background-color: #e0e0e0;
  border-radius: 5px;
  overflow: hidden;
}

.progress {
  height: 100%;
  background-color: #4caf50;
  transition: width 0.3s ease-in-out;
}

/* Responsive Design */
@media (max-width: 768px) {
  .image-container {
    flex-direction: column;
    gap: 20px;
  }

  .original-container,
  .processed-container {
    width: 100%;
  }
}
